import React, { Component } from 'react';
import axios from 'axios';
import Pokemon from "./Pokemon";
import {getLocation} from "./functions.js";

class PokemonList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            location: null,
            pokemon: []
        }
    }

    render() {

        const pokemon = this.state.pokemon.map((pokemon, i) => {
            return <Pokemon pokemon={pokemon} location={this.state.location} key={i}/>
        });

        return (
            <div className="Pokefy">
                {pokemon}
            </div>
        );
    }

    componentDidMount() {

        getLocation((location) => {
            this.setState({location});
        });

        axios.get('http://folk.ntnu.no/martsti/Pokefy/pokemon.php?type=all')
            .then( (response) => {
                console.log(response.data.pokemon);
                this.setState({pokemon : response.data.pokemon});
            })
            .catch((error) => {
                console.log(error);
            });
    }

}

export default PokemonList;


